import Home from './router/Home';
import Doc from './router/Doc';
import Tuto from './router/Tuto';
import Blog from './router/Blog';
import Comm from './router/Comm';
import Login from './router/Login';
import {Core,Guide,Api,Hooks} from './router/Doc'

const routes = [
    {
        path:'/',
        exact:true,
        component:Home
    },
    {
        path:'/doc',
        component:Doc,
        pri:true,
        routes:[
            {
                path:'/doc/core',
                component:Core
            },
            {
                path:'/doc/guide',
                component:Guide
            },
            {
                path:'/doc/api',
                component:Api
            },
            {
                path:'/doc/hooks',
                component:Hooks
            }
        ]
    },
    {
        path:'/tuto',
        component:Tuto,
        pri:true
    },
    {
        path:'/blog',
        component:Blog
    },
    {
        path:'/comm',
        component:Comm
    },
    {
        path:'/login',
        component:Login
    },
]


export default routes;