import React from 'react'

const Home = (props) => {
    console.log(props);
    return (
        <div style={{textAlign:'center'}}>
            <h3>React</h3>
            <p style={{fontSize:'14px'}}
            >用于构建用户界面的 JavaScript 库</p>
            <div>
                <button onClick={
                    ()=>props.history.push('/doc')
                }
                style={{backgroundColor:'#61DAFB',color:'#000',borderColor:'#61DAFB',fontSize:'18px',borderRadius:5}}
                >快速开始</button>
                <button onClick={
                    ()=>props.history.push('/tuto')
                }
                style={{backgroundColor:'#61DAFB',color:'#000',borderColor:'#61DAFB',fontSize:'18px',borderRadius:5}}
                >入门教程</button>
            </div>
        </div>
    )
}

export default Home